@font-face {
  font-family: 'Chalk';
  src:  url('/assets/fonts/ChalkPaint.ttf');
}

.current {
    color: green;
}
.parallax {
    /* The image used */
    min-height: 500px;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
   
    background-repeat: no-repeat;
    background-size: cover;
    border: 0;
    margin: 0;
    padding: 0;
    border: 0;

}

body, html {
    border: 0;
    margin: 0;
    padding: 0;
    border: 0;
    height: 100%;
    width: 100%;
}

.centered-section {
    margin: 0;
    position: relative;
    top: 50%;
    text-align: center;
}

.centered-section-opaque {
    background-color: black;
    margin: 0;
    position: absolute;
    top: 50%;
    text-align: center;
}

.home-section {
    height: 250px;

}

.center-container{
    text-align: center;
    width: 100%;
    position: absolute;
    top: 30%;

}

.transparent-bg {
    background-color: #000000AF;
    color: #F4E2B5;
    box-shadow: 0px 0px 20px black;
    text-shadow: 5px 5px 12px black;
    border-radious: 20px;
}

.darkgray-bg{
    background-color: #0F0F0FFF;
    color: #F4E2B5;
    box-shadow: 0px 0px 20px black;
    text-shadow: 5px 5px 12px black;
    border-radious: 20px;
    margin: 5px;
    padding: 5px;

}

.hover-shadow{
    transition: box-shadow .3s;
    border-radius:20px;
    transition: transform .2s;
}

.hover-shadow:hover{
    box-shadow: 0 0 11px rgba(0,0,0,1.0);
    transform: scale(1.05);
}


